import {inject} from 'aurelia-framework';
import {BLAdapter} from  'BLAdapter';


@inject(BLAdapter)
export class App {
  constructor(blAdapter) {
    this.message = 'Hello brave new World!';
    this.blAdapter = blAdapter;
    this.blAdapter.init();

    this.primaryColor = 'white';
    this.accentColor = 'darkblue';
  }

  configureRouter(config, router) {
    config.title = 'Au PWA';
    config.map([
      { route: ['', 'search'], name: 'search',  moduleId: 'search/search',  nav: false, title: 'Search' },
      { route: 'settings', name: 'settings',  moduleId: 'settings/settings',  nav: false, title: 'settings' },

      { route: 'ehr', name: 'ehr', moduleId: 'search/ehr', nav: true, title: 'Patient Records' },
      { route: 'literature',   name: 'literature',  moduleId: 'search/literature', nav: true, title: 'Literature' },
      { route: 'trial',   name: 'trial',          moduleId: 'search/trial', nav: true, title: 'Trials' }


    ]);

    this.router = router;
  }


}
