import {inject} from 'aurelia-framework';
import {Model} from 'model/Model';


@inject(Model)
export class User {
  heading = 'User Details';

  constructor(model) {
    http.configure(config => {
      config
        .useStandardConfiguration()
        .withBaseUrl('https://api.github.com/');
    });

    this.http = http;
    this.model = model;

  }

  syncState (state) {

  }

  attached() {  console.log('attached()', arguments) }
  detached() {  console.log('detached()', arguments) }
  bind()     {  console.log('bind()', arguments) }
  unbind()   {  console.log('unbind()', arguments) }


  activate(params, test) {
    subscribe('users/'+params.name, this.syncState.bind(this));

    console.log('activate', params.name);
    return this.http.fetch('users/'+params.name)
      .then(response => response.json())
      .then(user => this.name = user.login);


  }

}
