import {CssAnimator} from 'aurelia-animator-css';
import {inject} from 'aurelia-framework';

const menuClasses = {
  sliding_menu: ".sliding_menu",
  fade_screen: ".sliding_menu-fade-screen",
  menuVisible: "is_active"
}

@inject(CssAnimator, Element)
export class Search {
  constructor(animator, element) {
    this.animator = animator;
    this.element = element;
    this.menuVisible = false;
  }

  toggleSideMenu(){

    this.menuVisible = this.menuVisible ? false : true;

    console.log('animation_start');

    const menuElement = this.element.querySelector(menuClasses.sliding_menu);

    if (this.menuVisible) {
      this.animator.addClass(menuElement, menuClasses.menuVisible)
    } else {
      this.animator.removeClass(menuElement, menuClasses.menuVisible)
    }

  }

  configureRouter(config, router) {
    config.map([
      { route: ['', 'ehr'], name: 'ehr', moduleId: 'search/ehr', nav: false, title: 'Patient Record' },
      { route: 'literature',   name: 'literature',  moduleId: 'search/literature', nav: false, title: 'Literature' },
      { route: 'trial',   name: 'trial',          moduleId: 'search/trial', nav: false, title: 'Trials' }
    ]);
  }
}