@import "refills/search";
@import "refills/comments";
@import "refills/badges";
@import "components/sliding_menu";
@import "animation";

body {
  font-family: GillSans, Calibri, Trebuchet, sans-serif;
  @include outer-container;


}

nav {
  padding:  0 2vw 0 2vw ;

  border-bottom: 1px solid $base-border-color!important;

  height: 75px;

  margin-bottom:2vh;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  > ul {
    display: flex;
    flex-direction: row;

  }


}

.rounded {
  height: 50px;
  width: 50px;


  display: flex;
  align-items: center;
  justify-content: center;

  border: 1.5px solid $blue;
  border-radius: 50%;
  cursor: pointer;
  //fill: #76af93;
  text-align: center;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  z-index: 9999;

  font-size: 1.5rem;

  &.secondary {
    color: $base-border-color;
    border-color: $base-border-color;

  }

}

.results {
  @include padding(0 2vw 0 2vw);
}