.sliding_menu {
  position: fixed;
  z-index: 999;
  background-color: #67b5d1;
  //transition: transform 0.3s;

  top: 0;
  left: 0;
  @include transform(translateX(-100%));
  @include background(linear-gradient(red, green) left repeat);

  &.is_active {
   -webkit-animation: changeBack 3s;
    animation: changeBack 3s;

    width: 100%;
    height: 100%;
    overflow-y: scroll;

  }

}

.sliding_menu-fade-screen{
  position: fixed;
  z-index: 998;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 0;
  height: 0;
  background-color: #000;
  opacity: 0;
  transition: opacity 0.3s, width 0s 0.3s, height 0s 0.3s;

  &.is_active {
      width: 100%;
      height: 100%;
      opacity: 0.7;
      transition: opacity 0.3s;
  }
}

/**
 * Body states.
 *
 * When a menu is active, we want to hide the overflows on the body to prevent
 * awkward document scrolling.
 */
body.has-active-menu {
  overflow: hidden;
}



@-webkit-keyframes changeBack {
   0% { background-color: #e6efff; }
   25% { background-color: #4d91ff; }
   50% { background-color: #0058e6; }
   75% { background-color: #003180; }
   100% { background-color: #000a1a; }
}

@keyframes changeBack {
   0% { background-color: #000a1a; }
   25% { background-color: #003180; }
   50% { background-color: #0058e6; }
   75% { background-color: #4d91ff; }
   100% { background-color: #e6efff; }
}