import {inject} from 'aurelia-framework';
import {Model} from 'model/Model';
import {LogManager} from "aurelia-framework";
const log = LogManager.getLogger('ui.BLAdapter');

@inject(Model)
export class BLAdapter {

    constructor(model) {
        this.model = model;
    }


    receiveMessage(event) {
        log.info('received message', event.data);
    }

    postMessage(message) {
        log.info('post message', message);
        this.blWorker.postMessage(message);
    }

    init( ){
        // destroy old worker, if necessary
        if (this.blWorker) { this.blWorker.terminate() }

        // load new worker
        this.blWorker = new Worker('scripts/bl.bundle.js');
        this.blWorker.addEventListener('message', this.receiveMessage.bind(this))

        this.postMessage('hello');
    }

}